___CSS_LOADER_EXPORT___.push([module.id, "\n.editDiv[data-v-c2975e94] {\n    margin-top: 20px;\n    margin-left: 20px;\n    margin-right: 20px;\n}\n.editButtonDiv[data-v-c2975e94] {\n    border-top: 1px solid #E0E0E0;\n    margin-top: 30px;\n    margin-left: 20px;\n    margin-right: 20px;\n    text-align: end;\n}\n.updateBtn[data-v-c2975e94] {\n    margin-top: 20px;\n    margin-bottom: 20px;\n    width: 79px;\n    height: 32px;\n    border-radius: 4px;\n    opacity: 1;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    color: #FFFFFF;\n    letter-spacing: 0.02px;\n    opacity: 1;\n    background-color: #4F537B;\n    border: unset;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/editItem.vue"],"names":[],"mappings":";AAyGA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,kBAAkB;AACtB;AAEA;IACI,6BAA6B;IAC7B,gBAAgB;IAChB,iBAAiB;IACjB,kBAAkB;IAClB,eAAe;AACnB;AAEA;IACI,gBAAgB;IAChB,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,UAAU;IACV,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,cAAc;IACd,sBAAsB;IACtB,UAAU;IACV,yBAAyB;IACzB,aAAa;AACjB","sourcesContent":["<script setup lang=\"ts\">\nimport { ref } from 'vue'\nimport { container } from '../../../../domain/core/diContainer';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport IDEInput from \"../../components/application/IDEInput.vue\";\nimport { IIDEInputDefinitions } from '../../../../domain/presentation/IIDEInputDefinitions';\nimport { IObjectUseCase } from '../../../../domain/useCase/IObjectUseCase';\nimport { computed } from '@vue/reactivity';\nimport { IEditItemInline } from '../IComponentProps';\nimport { IFolderUseCase } from '../../../../domain/useCase/IFolderUseCase';\nimport { IModelUseCase } from '../../../../domain/useCase/IModelUseCase';\nimport { IViewModelManager } from '../../../../domain/viewModel/IViewModelManager';\n\nconst param = defineProps<{ param: IEditItemInline }>();\nconst props = param.param;\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst folderUseCase = container.resolve<IFolderUseCase>(IFolderUseCase);\nconst modelUseCase = container.resolve<IModelUseCase>(IModelUseCase);\n\nconst emit = defineEmits([\"close\"]);\nconst isNameValid = ref<boolean>(false);\nconst isDescriptionValid = ref<boolean>(true);\nconst isPrefixValid = ref<boolean>(true);\nconst validations = ref<Record<\"name\" | \"description\" | \"prefix\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 2, max: 40 }, availableCharacters: true, nonSpaceCharacter: true },\n    prefix: { availableCharacters: false, nonSpaceCharacter: true },\n    description: { minAndMaxLength: { min: 2, max: 100 }, availableCharacters: true, nonSpaceCharacter: false },\n});\n\nconst newName = ref(\"\");\nconst description = ref(\"\");\nconst prefix = ref(\"\");\n\n\nfunction edit() {\n    executor.execute(async () => {\n        switch(props.type) {\n            case \"folder\":\n                const folder = viewModelManager.findObject(props.id);\n                if (folder?.objectType != \"folder\") { throw new Error(\"unexpected type for folder: \" + folder?.objectType); }\n                await folderUseCase.updateFolder(folder, { name: newName.value });\n                break;\n            case \"model\":\n                await modelUseCase.updateModel(props.id, { name: props.modelType == \"css\" ? newName.value + \".css\" : newName.value }, { showNotification: true });\n                break;\n            case \"module\":\n                await objectUseCase.updateModule(props.id, true, {name: newName.value, description: description.value, prefix: prefix.value });\n                break;\n        }\n        \n        emit(\"close\");\n    }, { loading: true });\n}\n\nfunction setNameValue(value: string) {\n    newName.value = value\n}\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid\n}\n\nfunction setDescriptionValue(value: string) {\n    description.value = value\n}\nfunction setDescriptionValidationStatus(isValid: boolean) {\n    isDescriptionValid.value = isValid\n}\n\nfunction setPrefix(prefixValue: string) {\n    prefix.value = prefixValue\n}\nfunction setPrefixValidationStatus(isValid: boolean) {\n    isPrefixValid.value = isValid\n}\n\nconst buttonDisabled = computed(() => !isNameValid.value || !isDescriptionValid.value || !isPrefixValid.value);\n\n</script> \n\n<template>\n    <div class='editDiv'>\n        <IDEInput :isRequired=\"true\" @getValue=\"setNameValue\" @validateStatus=\"setNameValidationStatus\"\n            placeholder=\"\" :type=\"'text'\" info-text=\"Name\" :validations=\"validations.name\" :value=\"props.name.split('.')[0]\"  />\n        <div v-if=\"props.type == 'module'\">\n            <IDEInput :isRequired=\"false\" @getValue=\"setPrefix\" @validateStatus=\"setPrefixValidationStatus\"\n                placeholder=\"\" :type=\"'text'\" info-text=\"Prefix\" :validations=\"validations.prefix\" :value=\"props.prefix\" \n                info-hover=\"For applications using webSDK, the 'moduleUrlPrefix/moduleName should be entered, \n                which is the expected page url routing in the shell application where the webSDK is integrated\" />\n            <IDEInput :isRequired=\"false\" @getValue=\"setDescriptionValue\" @validateStatus=\"setDescriptionValidationStatus\"\n                placeholder=\"\" :type=\"'textarea'\" info-text=\"Description\" :validations=\"validations.description\" :value=\"props.description\"  />\n        </div>\n        <!-- <label> \n            Module Name\n            <input v-model=\"name\" placeholder=\"Name\" />  \n        </label>  -->\n    </div>\n    <div class=\"editButtonDiv\">\n        <button :style=\"{ opacity: buttonDisabled ? '0.5' : '' }\" class='updateBtn'\n                @click=\"edit()\" :disabled=\"buttonDisabled\">Update</button>\n    </div>\n</template> \n\n<style scoped>\n.editDiv {\n    margin-top: 20px;\n    margin-left: 20px;\n    margin-right: 20px;\n}\n\n.editButtonDiv {\n    border-top: 1px solid #E0E0E0;\n    margin-top: 30px;\n    margin-left: 20px;\n    margin-right: 20px;\n    text-align: end;\n}\n\n.updateBtn {\n    margin-top: 20px;\n    margin-bottom: 20px;\n    width: 79px;\n    height: 32px;\n    border-radius: 4px;\n    opacity: 1;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    color: #FFFFFF;\n    letter-spacing: 0.02px;\n    opacity: 1;\n    background-color: #4F537B;\n    border: unset;\n}\n</style>"],"sourceRoot":""}]);
